.button {
  font-size: 16px;
  font-weight: 500;
  text-transform: capitalize;
  background-color: lightcoral;
  width: 70px;
  border-radius: 4px;
  outline: none;
}
.button:hover,
.button:focus {
  background-color: lightgray;
}

.button:not(:last-child) {
  margin-right: 10px;
}
